<template>
   <div>
       <ul class="list">
         <p><input type="text" placeholder="           大家都在搜“我的高光又便宜又美丽”"/></p>
        <p><img src="/static/img/indexmain1.png" alt=""/></p>


          <li class="sp1" v-for="(shoplist,index) in shoplist" :key="index" >
              <img :src="shoplist.tp" alt="">
              <h4>{{shoplist.bt}}</h4>
              <span>{{shoplist.wz}}</span>
          </li>

            <li class="sp2" v-for="(shoplistitems, index) in shopList2" :key="index" >
              <img :src="shoplistitems.tp" alt=""/>
              <h4>{{shoplistitems.bt}}</h4>
              <span>{{shoplistitems.wz}}</span>
              <span>{{shoplistitems.jg}}</span>
            </li>
          
       </ul>
       
   </div> 
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
           shoplist: [],
           shopList2: []
        }
    },
    mounted() {
       let url = "./static/data/shoplist.json"

       Axios.get(url).then(res => {
           this.shoplist = res.data.shoplist;
           console.log(res, this.shoplist);
       }).catch(); 


      Axios.get(url).then(res => {
           this.shopList2 = res.data.shoplist2;
           console.log(res,this.shopList2);
       }).catch(); 



    }
    
}
</script>
<style scoped>
.list{margin-top: 16%;}
.list p{position:relative;}
.list p:nth-child(1) input{width: 5.5rem; height: 0.45rem;margin-left: 0.45rem;font-size: 0.2rem; background: #F5F5F5;border-radius: 0.5rem; border: 1px solid #F5F5F5;}
.list p:nth-child(2) img{width: 0.3rem;height: 0.3rem;}
.list p img{position: relative;top: -0.38rem;left: 0.7rem;}

.list .sp1{width: 2rem;height: 3.2rem;margin-left: 0.1rem; float: left;}
.list .sp1 img{width: 2rem;height: 2rem;}
.list .sp1 h4{text-align: center;font-weight: bold;font-size: 0.32rem;}
.list .sp1 span{display: inline-block;width: 2rem;height: 1rem;text-align: center; font-size: 0.23rem;color: #858585;}


.list .sp2{width: 3rem;height: 4.5rem; float: left;margin-left: 0.15rem;}
.list .sp2 img{width: 3rem;height: 2.8rem;}
.list .sp2 h4{font-size: 0.28rem;width: 3rem;height: 0.5rem;font-weight: bold;text-align: center;}
.list .sp2 span:nth-child(3){font-size: 0.23rem;display: inline-block;width: 3rem;height: 0.8rem;text-align: center;}
.list .sp2 span:nth-child(4){font-size: 0.23rem;font-weight: bold;display: inline-block;width: 3rem;height: 0.8rem;color: #ff0000;}

</style>



