<template>
<div>
       <ul class="find">
       <p>
       <input type="text" placeholder="         大家都在搜“我的高光又便宜又美丽”"/>
       </p>
       <p><img src="/static/img/indexmain1.png" alt=""/></p>


        <li class="find1"><span v-for="(find,index) in findnav" :key="index" >
        {{find.cd}}</span></li>

        <li v-for="(find1,index) in findnav2" :key="index" class="find2">
           
             <img :src="find1.tp" alt=""/>
             <span>{{find1.bt}}</span>
              <img :src="find1.tx" alt=""/>
             <span>{{find1.mz}}</span>
             <span>{{find1.x}}</span>
             <span>{{find1.zan}}</span>
        </li>
      
        
        </ul>
</div> 
</template>
<script>
import Axios from 'axios'

export default {
    data(){
        return {
           findnav:[],
           findnav2:[]
          
        }
    },
    mounted() {
       let url = "./static/data/find.json"

       Axios.get(url).then(res => {
           this.findnav = res.data.findlist1;
           console.log(res, this.findnav);
       }).catch(); 


        Axios.get(url).then(res => {
           this.findnav2 = res.data.findlist2;
           console.log(res, this.findnav2);
       }).catch(); 
    }
    
}
</script>







<style>
.find{margin-top: 16%;}
.find p{position:relative;}
.find p:nth-child(1) input{width: 5.5rem; height: 0.45rem;margin-left: 0.45rem;font-size: 0.2rem; background: #F5F5F5;border-radius: 0.5rem; border: 1px solid #F5F5F5;}
.find p:nth-child(2) img{width: 0.3rem;height: 0.3rem;}
.find p img{position: relative;top: -0.38rem;left: 0.7rem;}


.find .find1{background: #F9F9F9;width: 6.4rem;height: 0.5rem;margin-top: -0.15rem;}
.find .find1 span{display: inline-block;width: 0.7rem;margin-left: 0.1rem; color: #999999;font-size: 0.24rem;}


.find2{width: 3rem;height: 5.7rem; margin-top: 0.2rem;margin-left: 0.15rem;margin-top: 0.2rem; float: left;}
.find2 img:nth-child(1){display: inline-block;width: 3rem;height: 4rem;}
.find2 span:nth-child(2){display: inline-block;width: 3rem;height: 1rem;font-size: 0.23rem;}
.find2 img:nth-child(3){display: inline-block;width: 0.7rem;height: 0.7rem;}
.find2 span:nth-child(4){display: inline-block;width: 1rem; font-size: 0.2rem;margin-top: 0.2rem;}
.find2 span:nth-child(5){display: inline-block;color: hotpink;font-size: 0.23rem;margin-left: 0.3rem;}
.find2 span:nth-child(6){display: inline-block;font-size: 0.23rem;}


</style>




