<template>
    <div class="header" :style="{background: bg}">
        <span class="home" @click="goHome"></span>
          <h2 class="title">
          <slot name="title">webApp</slot> <!--具名插槽-->
        </h2>
    </div>
</template>

<script>
export default { 
  methods:{
    goHome(){
      this.$router.push('/')
    }
  },
  props: ['bg']
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 1rem;
  position: fixed;
  top: 0;
  line-height: 1rem;
  background:#fff;
  z-index:10;
}
.home {
  color: #fff;
  margin-left: 0.2rem;
}
.title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
</style>




