<template>
   <div>
       <ul>
        <li v-for="(human,index) in person1" :key="index" class="human">
        <img :src="human.tx" alt=""/>
        <span>{{human.nc}}</span>
        <span>{{human.num1}}</span>
        <span>{{human.num2}}</span>
        <span>{{human.num3}}</span>
        <span>{{human.gz}}</span>
        <span>{{human.fs}}</span>
        <span>{{human.hz}}</span>
        </li>
        <li class="human2">
        <input type="submit" value="个人资料"/>
        <span>等级：薯北鼻 ></span>
        <span>添加个人描述，可让大家更好地认识你</span>
        <span>笔记·0</span>
        <span>专辑·0</span>
        <input type="submit" value="发布你的第一篇笔记"/>
        </li>   
            
        </ul>
   </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            person1: []
        }
    },
    mounted(){
        let url = './static/data/person.json'

        Axios.get(url).then(res => {
            this.person1 = res.data.person;
            console.log(res,this.person1);
       }).catch(); 


    }
}
</script>

<style scoped>
.human{width: 6.4rem;height: 4rem;background: url("../../assets/img/bg.jpg");background-size: 6.4rem 6.4rem; }
.human img:nth-child(1){display: block; width: 1.1rem;height: 1.1rem;margin-left: 2.6rem;padding-top: 0.8rem;}

.human span:nth-child(2){display: block;width: 1.25rem;height: 0.35rem;font-size:0.25rem;margin-left: 2.55rem;margin-top: 0.2rem;color: #fff; }
.human span{color: #fff;}
.human span:nth-child(3){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}
.human span:nth-child(4){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}
.human span:nth-child(5){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}
.human span:nth-child(6){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}
.human span:nth-child(7){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}
.human span:nth-child(8){display: block;width: 1.3rem;height: 0.35rem;font-size:0.25rem;margin-left: 0.6rem;margin-top: 0.25rem;float: left;text-align: center;}


.human2{width: 6.4rem;height: 6.5rem;}
.human2 input:nth-child(1){width: 3rem;height: 0.4rem;margin-top: 0.6rem;margin-left: 1.7rem; background: #fff;color: #333333;border: 0.01rem solid#333333; border-radius: 1rem;font-size: 0.24rem;}
.human2 span:nth-child(2){display: block; width: 1.8rem;height: 0.4rem;margin-top: 0.6rem;margin-left: 2.2rem;color: #333333;font-size: 0.24rem;}
.human2 span:nth-child(3){display: block; width: 4.2rem;height: 0.4rem;margin-top: 0.3rem;margin-left: 1.1rem;color: #333333;font-size: 0.24rem;}
.human2 span:nth-child(4){display: inline-block; width: 0.9rem;height: 0.5rem;margin-top: 0.3rem;margin-left: 1.8rem;color: #333333;font-size: 0.3rem;}
.human2 span:nth-child(5){display: inline-block; width: 0.9rem;height: 0.5rem;margin-top: 0.3rem;margin-left: 0.8rem;color: #333333;font-size: 0.3rem;}
.human2 input:nth-child(6){width: 3rem;height: 0.4rem;margin-top: 0.8rem;margin-left: 1.7rem; background: #fff;color: red;border: 0.01rem solid red; border-radius: 1rem;font-size: 0.24rem;}
</style> 


