<template>
    <div>
        <ul class="movie-list" >
        <p><input type="text" placeholder="           大家都在搜“吃鸡初体验”"/></p>
        <p><img src="../../assets/img/indexmain1.png" alt=""/></p>

        <p><img src="../../assets/img/indexmain2.png" alt=""/></p>


		<h3>没有来自好友的内容</h3>
		<span>关注后，你可以在关注页面中看到对方的动态</span>
        <span>根据你的兴趣，为你推荐</span>
        <li v-for="(indexlist,index) in indexlist" :key="index">
           <img :src="indexlist.tp" alt=""/>
						<h5>{{indexlist.mz}}</h5>
            <input type="submit" value="关注"/>
					</li>
          
        
        </ul>          
    </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
          indexlist:[]
        }
    },
    mounted() {
       let url = "./static/data/indexlist1.json"

       Axios.get(url).then(res => {
           this.indexlist = res.data.indexlist1;
           console.log(res, this.indexlist);
       }).catch(); 
    }
    
}
</script>

<style scoped>
.movie-list{margin-top: 16%;}
.movie-list p{position:relative;}
.movie-list p:nth-child(1) input{width: 5.5rem; height: 0.45rem;margin-left: 0.45rem;font-size: 0.2rem; background: #F5F5F5;border-radius: 0.5rem; border: 1px solid #F5F5F5;}
.movie-list p:nth-child(2) img{width: 0.3rem;height: 0.3rem;}
.movie-list p:nth-child(2)  img{position: relative;top: -0.38rem;left: 0.7rem;}



.movie-list p:nth-child(3){width: 6.5rem; height: 2.3rem;}
.movie-list p:nth-child(3) img{width: 6.2rem; height: 2.3rem;margin-left: 0.1rem; }
.movie-list h3{font-size: 0.25rem;font-weight: bold;margin-left: 2.1rem;margin-top: 0.4rem;}
.movie-list span:nth-child(5){display: block;height: 0.3rem;;font-size: 0.23rem; margin-left: 1rem;margin-top: 0.6rem;}
.movie-list span:nth-child(6){display: block; text-align: center;font-size: 0.24rem;background-color: #F5F8FA;width: 5.6rem;border-radius: 0.5rem;margin-left: 0.5rem;margin-top: 0.5rem;}
.movie-list li{height: 1.2rem;margin-top: 0.5rem;margin-left: 0.3rem; }
.movie-list li img{display: block; width: 1.2rem;height: 1.2rem;float: left;}			
.movie-list li h5{width: 1.2rem;height: 1rem; text-align: center;font-weight:bold;margin-left: 0.15rem; margin-top: 0.1rem;font-size: 0.25rem; float: left;}
.movie-list li input{display: block;width: 1rem;height: 0.35rem; text-align: center;font-size: 0.2rem;margin-right: 5%;float: right; color:#ff0000;font-weight:bold;border:1px solid #FF5469;background-color: #fff;border-radius: 10%;}

</style>


