<template>
     <div >
        
              <ul class="ti">
                 <li v-for="(messages, index) in messagesList" :key="index" >
                      <img :src="messages.tp" />
                      <span>{{messages.wz}}</span>
                      <img :src="messages.jt" alt="" />
                 </li>
             </ul>  
     </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
           messagesList: []
        }
    },
    mounted() {
       let url = "./static/data/messages.json"
       console.log(url)
       Axios.get(url).then(res => {
           this.messagesList = res.data.messagesList;
           console.log(res, this.messagesList);
       }).catch(); 
    }
}
</script>

<style scoped>
.ti{margin-top: 1.2rem;}
.ti li {width: 6.4rem;height: 1.1rem;margin-top: 0.1rem;}
.ti li img:nth-child(1){width: 1.1rem;height: 1.1rem;margin-left: 0.1rem;float: left;}
.ti li span{display: inline-block;width: 2.2rem;height: 0.5rem;font-size: 0.3rem;margin-top: 0.27rem;margin-left: 0.2rem;}
.ti li img:nth-child(3){width: 0.3rem;height: 0.3rem;margin-left: 2.2rem;margin-top: 0.4rem;}
</style>





